import React, { Component } from 'react';
import Slider from "react-id-swiper";
import img1 from "../assets/2.jpg";
import img2 from "../assets/slider.jpg";
import img3 from "../assets/1.jpg";

const content = [
  {
    title: "Coming soon EMARTMALL",
    description:
      "Crediting isn’t required, but is appreciated and allows photographers to gain exposure.",
    imgnm: img1
  },
  { title: "Hello", description: "description", imgnm: img2 },
  { title: "Hello", description: "description", imgnm: img3 }
];

const sliderParams = {
  autoplay: {
    delay: 5000,
    disableOnInteraction: false,
  },
  pagination: {
    el: ".swiper-pagination",
    type: "bullets",
    clickable: true,
  },
  spaceBetween: 0,
};

class homepage extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <React.Fragment>
        <div className="homepage">
          <Slider {...sliderParams}>
            {content.map((article, index) => (
              <div
                key={index}
                className="slider-content"
                style={{ backgroundImage: `url(${article.imgnm})` }}
              >
                <div className="slider-text">
                  <span>
                    <h1>{article.title}</h1>
                    <div>{article.description}</div>
                  </span>
                </div>
              </div>
            ))}
          </Slider>
        </div>
      </React.Fragment>
    );
  }
}

export default homepage;