import React, { Component } from 'react';
import Container from '@material-ui/core/Container';


class homepage extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <React.Fragment>
        <Container>
          <div className="page-center">
            <h1>
              Хэрхэн <span>сагсанд</span> нэмэх вэ?
            </h1>
          </div>
        </Container>
      </React.Fragment>
    );
  }
}

export default homepage;